const initialState = {
  darkMode: false,
}

export default function themeReducer(
  state = initialState,
  action: {type: string, payload?: any}
) {
  switch (action.type) {
    case 'theme/changeMode': {
      return {
        ...state,
        darkMode: !state.darkMode,
      };
    }
    default:
      return state;
  }
}